html, body, #root{
  background-color: rgba(0, 45, 57, 0.75);
  color: white;
  height: 100%;
  width: 100%;
  margin: 0;
}

#calculator{
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  height: 100%;
}

#display{
  border: 5px solid white;
  border-radius: 1%;
}

button {
  text-align: center;
  line-height: 0;
  font-size: 18px;
  margin: 1px;
  height: 110px;
  width: 100px;
  border-radius: 5px;
  border: none;
}

#zero, #clear{
  width:202px;
}

#calcField{
  border: 1px solid black;
  overflow:auto;
  overflow-y: hidden;
  text-align: right;
  font-size: 62px;
  padding-right: 5px;
  background-color:grey;
  margin: 1px;
  height: 75px;
  width: 400px;
  
}

 

